<template>
  <div class="store-test">
    <h1>{{ msg }}</h1>
    <div class="box" @click="goB">点我跳转到B页面</div>
  </div>
</template>

<script>
export default {
  name: 'A',
  computed: {
  },
  data () {
    return {
      msg: 'Welcome to Your A PAGE'
    }
  },
  mounted () {
  },
  methods: {
    goB () {
    // 1、字符串this.$router.push('/B')
    // 2、对象 this.$router.push({ path: '/B' })
    // 3、命名的路由 this.$router.push({ name: 'B', params: { msg: this.msg }})
      this.$router.push('/B')
      this.$router.push({ path: '/B' })
      this.$router.push({ name: 'B', params: { msg: this.msg } })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .box {
    width: 100%;
    height: 100px;
    background: yellow;
  }
</style>



